import React, { Fragment } from 'react'
import './Style.css'
export const Contact = () =>{
    return(
        <Fragment>
            <div className="contact">
                <div className="info-perso">
                    <h3>Laissez-nous vos informations</h3>
                    <div className="info-details">
                        <form>
                            <div className="form-class">
                                <label>Votre Nom</label>
                                <input type="text" />
                            </div>
                            <div className="form-class">
                                <label>Votre e-mail</label>
                                <input type="text" />
                            </div>
                            <div className="form-class">
                                <label>Sujet</label>
                                <input type="text" />
                            </div>
                            <div className="form-class">
                                <label>Message</label>
                                <textarea></textarea>
                            </div>
                        </form>
                    </div>
                </div>
                <div className="coord">
                    <h3>Coordonnées</h3>
                    <div>

                    </div>
                </div>
            </div>
        </Fragment>
    )
}