import React, { Fragment} from 'react'
import {Link} from 'react-router-dom'
import './StyleMobile.css'
export const NavBarMobile = (props) =>{
    const handleMenuLeft = ()=>{
        if(props.classNavbar.length > 0){
            props.setClassNavbar('')
        }
    }
    return(
        <Fragment>
            <footer id="navbar-mobile">
                <nav className="navbar-mobile">
                    <ul className="navbar-mobile-list">
                        <li className="list-menu"><Link to="/"><img className="img" src="./images/iconeMenu/Home.png" alt="" /><span className="text-menu">Home</span></Link></li>
                        <li className="list-menu"><Link to="/mes-services"><img className="img" src="./images/iconeMenu/Services.png" alt="" /><span className="text-menu">Service</span></Link></li>
                        <li className="list-menu"><Link to="/education"><img className="img" src="./images/iconeMenu/cv.png" alt="" /><span className="text-menu">Education</span></Link></li>
                        <li className="list-menu"><Link to="/porfolio"><img className="img" src="./images/iconeMenu/Portfolio.png" alt="" /><span className="text-menu">Portfolio</span></Link></li>
                        <li className="list-menu"><Link to=""><img className="img" src="./images/iconeMenu/Blog.png" alt="" /><span className="text-menu">Blog</span></Link></li>
                        <li className="list-menu"><Link to="/contact"><img className="img" src="./images/iconeMenu/Contact.png" alt="" /><span className="text-menu">Contact</span></Link></li>
                    </ul>
                </nav>
            </footer>
            {/* Barre menu qui se trouve en haut */}
            <header className="bar-responsive">
                <div className="bar-responsive-list">
                    <p className="menu-hamburger" onClick={()=>handleMenuLeft()}>
                    <svg stroke="currentColor" fill="none" stroke-width="2" viewBox="0 0 24 24" stroke-linecap="round" stroke-linejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg" aria-expanded="false"><line x1="3" y1="12" x2="21" y2="12"></line><line x1="3" y1="6" x2="21" y2="6"></line><line x1="3" y1="18" x2="21" y2="18"></line></svg>
                    </p>
                    <p>
                    <svg stroke="currentColor" fill="currentColor" stroke-width="0" viewBox="0 0 512 512" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg"><path d="M253.125 18.563c-131.53 0-238.375 106.813-238.375 238.343 0 131.53 106.846 238.344 238.375 238.344 131.53 0 238.344-106.815 238.344-238.344 0-131.528-106.816-238.344-238.345-238.344zm-23.938 52.093c40.517 0 77.988 12.904 108.532 34.813-5.597-.624-11.302-.97-17.064-.97-84.157 0-152.375 68.25-152.375 152.406 0 84.157 68.22 152.375 152.376 152.375 5.762 0 11.467-.313 17.063-.936-30.545 21.91-68.016 34.812-108.533 34.812-102.98 0-186.28-83.272-186.28-186.25 0-102.977 83.3-186.25 186.28-186.25z"></path></svg>
                    </p>
                    <p className="icone-right">
                    <svg stroke="currentColor" fill="none" stroke-width="2" viewBox="0 0 24 24" stroke-linecap="round" stroke-linejoin="round" height="1em" width="1em" xmlns="http://www.w3.org/2000/svg" aria-expanded="false"><circle cx="12" cy="12" r="1"></circle><circle cx="12" cy="5" r="1"></circle><circle cx="12" cy="19" r="1"></circle></svg>
                    </p>
                </div>
            </header>
        </Fragment>
    )
}