import './App.css';
import { Home } from './component/Home';
import { BrowserRouter as Router,Route } from 'react-router-dom';
import { MyService } from './component/MyServices/MyService';
import { Education } from './component/Education/Education';
import { Portfolio } from './component/Portfolio/Portfolio';
import { Contact } from './component/Contact/Contact';
import { NavBarMobile } from './component/Navbar/NavBarMobile';

function App() {
  return (
    <div className="App">
      <Router>
        <NavBarMobile />
        <Route path="/" exact component={Home} />
        <Route path="/mes-services" exact component={MyService} />
        <Route path="/education" exact component={Education} />
        <Route path="/porfolio" exact component={Portfolio} />
        <Route path="/contact" exact component={Contact} />
      </Router>
    </div>
  );
}

export default App;
