import React, { Fragment } from 'react'
import './Style.css'
export const Education = ()=>{
    return(
        <Fragment>
            <section className="education">
                <div>
                    <h1 className="nameEducation">Éducation</h1>
                    <p className="citation">text</p>
                </div>
                <section className="school-year">
                    <div className="year-student">
                        <div className="student">
                            <p className="student-text">Etudiant</p>
                            <p className="year">2018 - 2019</p>
                        </div>
                        <div className="student">
                            <p className="student-text">Etudiant</p>
                            <p className="year">2019 - 2020</p>
                        </div>
                        <div className="student">
                            <p className="student-text">Etudiant</p>
                            <p className="year">2020 - 2021</p>
                        </div>
                    </div>
                    <div className="school">
                        <div>
                            <h2>Licence 1 Génie informatique</h2>
                            <span>Univers Professionnel(Unipro,Dakar)</span>
                        </div>
                        <div>
                            <h2>Licence 2 Génie informatique</h2>
                            <span>Univers Professionnel(Unipro,Dakar)</span>
                        </div>
                        <div>
                            <h2>Licence 3 Génie informatique</h2>
                            <span>Univers Professionnel(Unipro,Dakar)</span>
                        </div>
                    </div>
                </section>
            </section>
        </Fragment>
    )
}